import React, { Component } from 'react'

class Home extends Component {
  render () {
    return (
      <div className='home fade-in'>
        <p>
          Ready to embark on a magickal journey? <br/>
          <br/>
          A journey filled with drama, victory, and accomplishment?<br/>
          <br/>
          A break from the mundane?<br/>
          <br/>
          Then listen closely friend, you are ready for.....
        </p>
        <br/>
        <p className='emphasize'>
          NEXT LEVEL FITNESS
        </p>
        <br/>
        <p>
          Here you can pick your character, add your workouts, and see your gains
          immediately on your character. So get up, and start moving, theres no
          time to waste. The world needs your help hero!
          <br/>
          <br/>
          Sign up above to begin your quest today!
        </p>
      </div>
    )
  }
}

export default Home



